/*Countdown start here*/

.count-down {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  text-align: center;
  height: auto;
}

.count-down div {
  margin: 30px 0 50px 0;
  padding: 0px;
  list-style: none;
  font-size: 28px;
  display: inline-block;
}

.count-down div input {
  font-family: 'Roboto', sans-serif !important;
  font-size: 60px!important;
  width: 154px!important;
  height: 98px!important;
  margin: 30px 0 0 -165px !important;
  font-weight: normal!important;
}

input.knob {
  margin: 0px !important;
}

#days-title,
#hours-title,
#mins-title,
#secs-title {
  display: block;
  text-transform: uppercase;
  margin-top: -94px;
  font-size: 20px;
  position: relative;
  z-index: 9 !important;
}

/*Countdown end here*/

.logo {
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 20px;
  max-width: 70%;
  padding: 0 10%;
}

h2.coming {
  font-size: 50px;
}

h5 {
  font-size: 16px;
}

a.social {
  display: block;
  font-size: 18px;
  opacity: .75;
  transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
}

a.social:hover {
  opacity: 1;
  transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
}

a.social img {
  width: 30px;
  height: auto;
  display: inline-block;
  margin-left: 5px;
  margin-top: 5px;
  position: relative;
  vertical-align: middle;
}

/* Responsive Media */

@media (max-width: 600px) {
  .count-down {
    margin: 0px 0px 20px 0px;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    height: auto;
  }

  .count-down div {
    margin: 20px 0 30px 0;
    padding: 0;
    list-style: none;
    font-size: 28px;
    display: inline-block;
  }

  .count-down div input {
    font-family: 'Roboto', sans-serif !important;
    font-size: 60px!important;
    width: 150px!important;
    height: 150px!important;
    margin: -20px 0 0 -150px !important;
    font-weight: normal!important;
  }

  input.knob {
    margin: 0px !important;
  }

  #days-title,
  #hours-title,
  #mins-title,
  #secs-title {
    max-height: 100%;
    max-width: 100%;
    display: block;
    text-transform: uppercase;
    margin-top: -65px;
    font-size: 60%;
    position: relative;
    text-align: center;
  }

  .logo {
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-top: 10px;
    max-width: 70%;
    padding: 0%;
  }

  h2.coming {
    font-size: 24px;
  }

