}

@media (min-width: 600px) and (max-width: 992px) {
  h2.coming {
    font-size: 36px !important;
  }

  .count-down div {
    margin: 30px 0 50px 0;
  }
}

/*Scroll Navbar*/

.navbar-fixed {
  height: 0px;
}

#index-banner {
  padding-top: 40px;
  min-height: 100%;
  background-image: url('../img/bg.png');
  background-position: 20% 100%;
  background-repeat: repeat-x;
  position: relative;
}

.scrollspy {
  min-height: 91%;
}

img.responsive-img {
  height: 200px!important;
}

.view {
  margin-top: 50px;
}

.container-mod {
  margin: 0 auto;
  padding: 20px 0;
  max-width: 1280px;
  width: 80%;
}

@media only screen and (min-width: 601px) {
  .container-mod {
    width: 95%;
  }
}

@media only screen and (min-width: 993px) {
  .container-mod {
    width: 90%;
  }
}

